import { Link, useParams } from "react-router-dom";
import { useState } from "react";
import { getHelloWorldBeanPathVariable } from "./api/HelloWorldApiService";
import { useAuth } from "./security/AuthContext";
import { useTranslation } from "react-i18next";

function WelcomeComponent() {
  //LoginComponent  {username} url  
  const { username } = useParams();
  // Reatapi    Hook
  const [message, setMessage] = useState(null);
  //token  context 
  const authContext = useAuth();
  //  
  const { t } = useTranslation();
  //axios  restapi  
  function callHelloWorldRestApi() {
    console.log("called");
    //HTTP GET Request hello-world
    // axios.get('http://localhost:8080/hello-world')
    //      .then( (response) => successfulResponse(response) )
    //      .catch( (error) => errorResponse(error) )
    //      .finally( () => console.log('clean up') )

    //axios    
    // getHelloWorldBean()
    // .then( (response) => successfulResponse(response) )
    // .catch( (error) => errorResponse(error) )
    // .finally( () => console.log('clean up') )

    //username  api    username, token argument 
    getHelloWorldBeanPathVariable(authContext.username, authContext.token)
      .then((response) => successfulResponse(response))
      .catch((error) => errorResponse(error))
      .finally(() => console.log("clean up"));
  }

  function successfulResponse(response) {
    console.log(response);
    //api    setMessage 
    setMessage(response.data.message);
  }

  function errorResponse(error) {
    console.log(error);
  }

  return (
    <div className="Welcome">
      <h1>
        {t("welcome")} {username}!
      </h1>
      <div>
        {/*    WelcomeComponent ListTodosComponent   Link   */}
        Todo {t("list_page")} - <Link to="/todos">{t("go_link")}</Link>
      </div>
      <div>
        {/* RestApi    */}
        <button className="btn btn-success m-5" onClick={callHelloWorldRestApi}>
          API {t("call_test")}
        </button>
      </div>
      <div className="text-info">
        {/* RestApi message   */}
        {message}
      </div>
    </div>
  );
}

export default WelcomeComponent;
