import "./LoginComponent.css";
//       hook
import { useState } from "react";
//useNavigate: (navigation)    . (v6 )
import { useNavigate } from "react-router-dom";
import { useAuth } from "./security/AuthContext";
import { useTranslation } from "react-i18next";

function LoginComponent() {
  //useState Hook      setUserName
  const [username, setUserName] = useState("");
  //useState Hook      setPassword
  const [password, setPassword] = useState("");

  //   useState  false
  const [showErrorMessage, setErrorMessage] = useState(false);
  //   useNavicate 
  const navigate = useNavigate();
  // useContext Hook 
  const authContext = useAuth();
  //  
  const { t } = useTranslation();

  function handleUserNameChange(event) {
    // console.log(event.target.value);
    //username input     setUserName 
    setUserName(event.target.value);
  }

  function handlePasswordChange(event) {
    // console.log(event.target.value);
    //password input     setPassword 
    setPassword(event.target.value);
  }
  // async hanleSubmit   
  async function hanleSubmit() {
    //      true
    if (await authContext.login(username, password)) {
      //       `` 
      //   welcomeComponet  {username}   
      navigate(`/welcome/${username}`);
    } else {
      //      true 
      setErrorMessage(true);
    }
  }

  //Login component return - login form 
  return (
    <div className="Login">
      {/*     
            <SuccessMessageComponent/>
            {/*      */}
      {/* <ErrorMessageComponent/> */}

      {/*         */}
      <section className="login-notice">
        <ul>
          <li>{t("login_notice_1")}</li>
          <li>{t("login_notice_2")}</li>
          <li>{t("login_notice_3")}</li>
          <li>
            <a href="http://yoon-react-client.s3-website.ap-northeast-2.amazonaws.com/">
              {t("login_notice_4")}
            </a>
          </li>
        </ul>
      </section>
      {/* <h6>      .</h6>
            <h6>(fly.io)     .</h6>
            <h6>        1~2   .</h6>
            <h6><a href="http://yoon-react-client.s3-website.ap-northeast-2.amazonaws.com/"> 1(AWS S3+EC2)</a></h6> */}
      <h1>{t('app_title')}</h1>
      <br />
      <h1>Test ID: yoon PW: dummy</h1>
      <h1>{t('jwt_login')}</h1>
      {/*    true    */}
      {showErrorMessage && (
        <div className="errorMessage">  IDPW </div>
      )}

      <div className="LoginForm">
        <div>
          <label>ID: </label>
          <input
            type="text"
            name="username"
            value={username}
            onChange={handleUserNameChange}
          />
        </div>
        <div>
          <label>PW: </label>
          <input
            type="password"
            name="password"
            value={password}
            onChange={handlePasswordChange}
          />
        </div>
        <div>
          {/*onClick     */}
          <button type="button" name="login" onClick={hanleSubmit}>
            Login
          </button>
        </div>
      </div>
    </div>
  );
}

export default LoginComponent;
